@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
:root {
  --contBox-border-radius : 3px;
  --contBox-border : 1.5px solid #f0f4f5;
  --white : #f0f4f5;
  --bgDeep : #000000;
  --bgLow : #1f222a;
}

* {margin : 0; padding : 0; box-sizing : border-box; text-decoration: none; color : #f0f4f5; color : var(--white); font-family: 'Noto Sans KR', sans-serif;}


body {
  background : #000000;
  background : var(--bgDeep);
  overflow-y : scroll;
}

body .displayNone {
  display : none;
}
body .displayBlock {
  display : block;
}
body .displayFlex {
  display : flex;
}
.absolute {
  position : absolute;
}
.imgWidth {
  display : block;
  width : 100%;
}
.equipMainImg {
  top : 0;
  transform : scale(.9);
}
.textCenter {
  text-align : center;
}

.height100vh {height : 100vh; overflow : hidden;}
.vw07 {font-size : 0.7vw;}
.rem07 {font-size : 0.7rem;}
.rem08 {font-size : 0.8rem;}
.rem09 {font-size : 0.9rem;}
.rem1 {font-size : 1rem;}
.rem11 {font-size : 1.1rem;}
.rem12 {font-size : 1.2rem;}
.rem13 {font-size : 1.3rem;}
.rem17 {font-size : 1.7rem;}
.zIndex10 { z-index : 10; }
.zIndex11 { z-index : 11; }
.zIndex99 { z-index : 99; }
.gradient-1 { background : linear-gradient(135deg, #232323, #7a0000); }
.gradient0 { background : linear-gradient(135deg, #232323, #575757); }
.gradient1 { background : linear-gradient(135deg, #18220b, #304911); }
.gradient2 { background : linear-gradient(135deg, #111f2c, #113d5d); }
.gradient3 { background : linear-gradient(135deg, #261331, #480d5d); }
.gradient4 { background : linear-gradient(135deg, #362003, #9e5f04); }
.gradient5 { background : linear-gradient(135deg, #341a09, #a24006); }
.color0, .color0 * { color : #a19f9f !important;}
.color0bold, .color0bold * { color : #a19f9f !important;  font-weight: bold;}
.color1, .color1 * { color : #4e8d01 !important;}
.color1bold, .color1bold * { color : #4e8d01 !important;  font-weight: bold;}
.color2, .color2 * { color : #0063aa !important;}
.color2bold, .color2bold * { color : #0063aa !important;  font-weight: bold;}
.color3, .color3 * { color : #700099 !important;}
.color3bold, .color3bold * { color : #700099 !important;  font-weight: bold;}
.color4, .color4 * { color : #ff9500 !important;}
.color4bold, .color4bold * { color : #ff9500 !important;  font-weight: bold;}
.color5, .color5 * { color : #ff5e00 !important;}
.color5bold, .color5bold * { color : #ff5e00 !important;  font-weight: bold;}
.overflowDot {
  text-overflow: ellipsis;
  overflow: hidden; 
  white-space: nowrap;
}
.hoverTarget:hover .hoverContent {
  display : block;
}


@media all and (min-width:768px) {
  html { font-size : 16px;}
}
@media screen and (max-width: 767px){
  html { font-size : 14px; }
}
@media screen and (max-width: 400px){
  html { font-size : 10px; }
}
.innerApp {
  padding : .5rem;
}
.loadingBg {
  width : 100%;
  position : absolute;
  top : 0;
  min-height : 100vh;
  background : rgba(0,0,0,.7);
}
.AppWrap {
  max-width : 600px;
  min-width : 310px;
  margin : 0 auto;
  background : var(--bgLow);
  border-radius : var(--contBox-border-radius);
  padding : .5rem;
}


/* home section 공통 */
.homeSection {
  margin-bottom : 20px;
}
.homeSectionTitle {
  margin-bottom : 10px;
  background : var(--bgDeep);
  padding : .3rem;
  border-radius : 3px 3px 0 0;
}
/* header */
.header {
  width : 100%;
  margin-bottom : 1.5rem;
}
.innerHeader {
  max-width : 600px;
  margin : 0 auto;
  padding : .5rem;
}
.logo {
  background : url("https://github.com/sangmin802/loa-hands/blob/master/public/img/all.png?raw=true");
  background-position : 403px 3.5px;
  width : 160px;
  margin : 0 auto 10px;
  height : 50px;
  cursor : pointer;
}
.logo * {
  display : inline-block; width : 100%; height : 100%;
}
.searchForm {
  display : flex;
  margin : 0 auto;
  width : 60%;
  height : -webkit-fit-content;
  height : -moz-fit-content;
  height : fit-content;
}
input[type=text] {
  width : 80%;
  background : none;
  border : 0;
  border-bottom : var(--contBox-border);
  padding : .3rem;
}
input:focus {
  outline : none;
  background : none;
  border : 0;
  border-bottom : var(--contBox-border);
}
input[type=submit] {
  width : 20%;
  background : none;
  border : 0;
  border : var(--contBox-border);
  font-weight : bold;
  border-radius : 3px 3px 3px 0;
  padding : .3rem;
  cursor : pointer;
}
.event {
  width : 49%;
  border : var(--contBox-border);
  border-radius : var(--contBox-border-radius);
  overflow : hidden;
  margin-bottom : 2%;
  cursor : pointer;
}
.eventContent {
  padding : .3rem;
  background : var(--bgDeep);
}
.eventsWrap {
  display : flex;
  justify-content : space-between;
  flex-wrap : wrap;
}
.timer {
  width : 32%;
  width : calc((100% - 4%) / 3 - 0.01px);
  margin-right : 2%;
  margin-bottom : 2%;
  border : var(--contBox-border);
  border-radius : var(--contBox-border-radius);
  overflow : hidden;
  position : relative;
}
.timer:nth-child(3n){
  margin-right : 0;
}
.endTimeBg {
  position : absolute;
  z-index : 1;
  width : 100%;
  height : 100%;
  display : none;
  background : rgba(0,0,0,.5);
}
.timerName {
  background : var(--bgDeep);
  padding : .1rem;
}
.timerContent {
  display : flex;
  justify-content : space-between;
  align-items : center;
  padding : .3rem;
}
.timerImg {
  width : 25%;
  position : relative;
}
.timerLv {
  position : absolute;
  bottom : 0;
  right : 0;
  background : rgba(0,0,0,.5);
  padding : .1rem;
  border-radius : var(--contBox-border-radius);
}
.timerTime {
  width : 70%;
  display : flex;
  flex-direction : column;
  justify-content : center;
  align-items: center;
}
.timeOut {
  color : #FF9933;
}
.timerWrap {
  display : flex;
  flex-wrap : wrap;
}
.calendar0 {
